<template>
  <div id="note_view">
    <head-top title="笔记详情"></head-top>
    <note-view :note-item="noteItem" :is-class="false" @toDelete="confirmDelete" @toEdit="handleEdit">      
    </note-view>
  </div>
</template>

<script>
import NoteView from "@/components/r-note/r-note-view";

import { Dialog } from "vant";
export default {
  name: "",
  components: { NoteView, [Dialog.Component.name]: Dialog.Component },
  data() {
    return {
      noteItem: {
        courseID: this.$route.query.courseID || "",
        courseName: this.$route.query.courseName || "",
        classHourID: this.$route.query.classHourID || "",
        classHourName: this.$route.query.classHourName || "",
        title: this.$route.query.title || "",
        content: this.$route.query.content || "",
        createTime: this.$route.query.createTime || ""
      }
    };
  },
  methods: {
    confirmDelete() {
      Dialog.confirm({
        title: "警告",
        message: "确定删除此笔记？"
      })
        .then(() => {
          this.dataDelete();
        })
        .catch(() => {
          // on cancel
        });
    },
    dataDelete() {
      this.$mainHttp.note
        .postDelete({ snid: this.$route.query.snID })
        .then(res => {
          if (res.Result) {
            this.$toast.success("删除成功!");
            setTimeout(() => {
              this.$router.push("/space/note");
            }, 200);
          } else {
            this.$toast.fail("操作失败");
          }
        })
        .catch(err => {
          this.$toast("操作失败");
        });
    },
    handleEdit() {
      this.$router.push({
        path: "/space/noteEdit",
        query: {
          ...this.$route.query
        }
      });
    }
  }
};
</script>

<style lang='less' scoped>
#note_view {
  width: 100%;
  height: 100%;
  background: #fff;
}
</style>


